/**
Select

Styl pro select
Pro více funkcí select inputu je použit doplněk <strong>select2</strong> https://select2.github.io/

Markup:
<select name="year" id="year" class="form-select" data-placeholder="Klikněte zde">
	<option></option>
	<option value="2015">2015</option>
	<option value="2014">2014</option>
	<option value="2013">2013</option>
	<option value="2012">2012</option>
</select>

Styleguide 7.2.
*/

.form-select {
	.f-base;
	border-radius: 0;
	.f-14;
}
.select2-container {
	width: 100%;
	
 	.select2-choice {
		border-radius: 0;
		border-color: @input-border;
		background: #fff none;
		height: 40px;
		line-height: 40px;
		.p-l(14px);

		.select2-chosen {
			.m-r(55px);
			.f-12;
		}
		.select2-arrow {
			width: 40px;
			border-radius: 0;
			border-color: @input-border;
			background: #fff none;

			b {
				background: url('/images/select2.png') center center no-repeat;
	    		background-repeat: no-repeat !important;
	        	background-size: 12px 7px !important;
			}
		}

		abbr {
			top: 50%;
			left: 50%;
			.m-t(-3px);
			.m-l(-6px);
    		background: url('/images/select2.png') center center no-repeat;
	    	background-repeat: no-repeat !important;
	       	background-size: 12px 7px !important;
		}
	}
}

.select2-drop.select2-drop-above.select2-drop-active,
.select2-dropdown-open.select2-drop-above .select2-choice,
.select2-dropdown-open.select2-drop-above .select2-choices,
.select2-drop-active {
	border-color: @input-border;
}

.select2-dropdown-open.select2-drop-above .select2-choice,
.select2-dropdown-open.select2-drop-above .select2-choices {
	background: #fff;
}
/* Retina-ize icons */

@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min-resolution: 2dppx)  {
    .select2-search input,
    .select2-search-choice-close,
    .select2-container .select2-choice abbr,
    .select2-container .select2-choice .select2-arrow b {
        background-image: url('/images/select2_@2X.png') !important;
        background-repeat: no-repeat !important;
        background-size: 12px 7px !important;
    }

    .select2-search input {
        background-position: 100% -21px !important;
    }
}