/**
Responzivní obrázek

Markup:
<img class="img-responsive" src="http://satyr.io/900x75/16" title="" alt="">

Styleguide 14.1.
*/

.img-responsive {
	display: block;
	width: 100%;
	height: auto;
	margin: 0;
}


/**
Odkaz s responzivním obrázkem

Markup:
<a href="#" class="img-link">
	<img src="http://satyr.io/900x75/17" title="" alt="">
</a>

Styleguide 14.2.
*/

.img-link {
	display: block;
	.m-clearfix();
	text-align: center;
	margin: 0 auto;

	img { .img-responsive; }
}