.c-tile-row { 
	.m-clearfix; 
	.m-l(5px);
	.m-r(5px);

	@media @small {
		.m-t(-10px);
		.m-l(-10px);
		.m-r(-10px);
	}
}

.column-full .c-tile-row {
	@media @small {
		.m-t(0);
	}
}
 
.c-tile-group {
	width: 100%;
	display: block;
  	position: relative;
	.m-clearfix;
	float: left;

	&.mod-big {
		width: 100%; 
		.c-tile-item {
			&.mod-width-1 { width: 25%; }
			&.mod-width-2 { width: 50%; }
			&.mod-width-4 { width: 100%; }
		}
		@media @mini { 
			width: 66.666666%; 
			.c-tile-item {
				&.mod-width-1 { width: 25%; }
				&.mod-width-2 { width: 50%; }
				&.mod-width-4 { width: 100%; }
			}
		}
		@media @medium { 
			width: 60%; 
			.c-tile-item {
				&.mod-width-1 { width: 16.666666%; }
				&.mod-width-2 { width: 33.333333%; }
				&.mod-width-4 { width: 66.666666%; }
			}
		}
	}

	&.mod-small {
		width: 100%; 
		.c-tile-item {
			&.mod-width-1 { width: 25%; }
			&.mod-width-2 { width: 50%; }
			&.mod-width-4 { width: 100%; }
		}
		@media @mini {
			width: 33.333333%; 
			.c-tile-item {
				&.mod-width-1 { width: 50%; }
				&.mod-width-2 { width: 100%; }
				&.mod-width-4 { width: 100%; }
			}
		}
		@media @medium { 
			width: 40%; 
			.c-tile-item {
				&.mod-width-1 { width: 25%; }
				&.mod-width-2 { width: 50%; }
				&.mod-width-4 { width: 100%; }
			}
		}
	}

	&.mod-full {
		width: 100%;
		.c-tile-item {
			&.mod-width-1 { width: 25%; }
			&.mod-width-2 { width: 50%; }
			&.mod-width-4 { width: 100%; }
		}
		@media @mini {
			width: 100%; 
			.c-tile-item {
				&.mod-width-1 { width: 16.666666%; }
				&.mod-width-2 { width: 33.333333%; }
				&.mod-width-4 { width: 66.666666%; }
			}
		}	
		@media @medium { 
			width: 100%; 
			.c-tile-item {
				&.mod-width-1 { width: 10%; }
				&.mod-width-2 { width: 20%; }
				&.mod-width-4 { width: 40%; }
			}
		}
	}

	&.mod-side-full {
		width: 100%;
		.c-tile-item {
			&.mod-width-1 { width: 25%; }
			&.mod-width-2 { width: 50%; }
			&.mod-width-4 { width: 100%; }
		}
		@media @mini {
			width: 100%; 
			.c-tile-item {
				&.mod-width-1 { width: 25%; }
				&.mod-width-2 { width: 50%; }
				&.mod-width-4 { width: 100%; }
			}
		}	
		@media @medium { 
			width: 100%; 
			.c-tile-item {
				&.mod-width-1 { width: 12.5%; }
				&.mod-width-2 { width: 25%; }
				&.mod-width-4 { width: 50%; }
			}
		}
	}

	&.mod-right { float: right; }


	// rozmístění dlaždic s jednou 4x4 dlaždicí uprostřed obtékanou po stranách 2mi 2x2 dlaždicemi
	&.mod-group-center {
		.c-tile-item:last-child {

			@media @medium {
				position: relative;
				top: -165px;
				margin-bottom: -165px;
			}
			@media @large {
				position: relative;
				top: -240px;
				margin-bottom: -240px;
			}
		}
	}
}

.c-tile-item {
	width: 100%;
	float: left;
	background: transparent;
	box-sizing: border-box;
	position: relative;
	z-index: 9;

	&:after {
		padding-top: 100%;
		/* 1:1 ratio */
		display: block;
		content: '';
	}

	&.mod-height-half:after { padding-top: 50%; /* 2:1 ratio */ }
	&.mod-height-2:after { padding-top: 200%; /* 1:2 ratio */ }
	&.mod-height-4:after { padding-top: 400%; /* 1:4 ratio */ }
	
	&.mod-right { float: right; }
	
	&.mod-nosquare {
		&:after {
			display: none;
		}
		@media @small {
			&:after {
				padding-top: 100%;
				/* 1:1 ratio */
				display: block;
				content: '';
			}
		}
	}

	&.mod-nosquare-ever {
		&:after {
			display: none;
		}
	}

	.c-tile-item-link {	
		.m(5px);
		display: block;	
		background-color: @midBlue;
		background-position: top center;
		background-repeat: no-repeat;
		background-size: 100% 100%;
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		z-index: 10;	
		transition: margin 160ms linear;
		text-decoration: none;
		overflow: hidden;

		@media @small {
			.m(10px);
		}

		&.mod-gradient:before {
			display: block;
			position: absolute;
			z-index: 11;
			content: '';
			top: 0;
			bottom: 0;
			right: 0;
			left: 0;
			background: linear-gradient(0deg, #101B22 0%, rgba(26, 44, 55, 0) 100%);
		}

		&.mod-white-background {
			background: #fff;
			.c-tile-content {
				h2 {
					text-shadow: none;
					color: @text;
				}
				p {
					text-shadow: none;
					color: @grey;
				}
			}
		}

		&.mod-without-photo {
			.c-tile-content {
				p {
					.f-condensed-bold;
					margin: 0;
					bottom: auto;
					top: 0;
					width: 100%;
					padding: 0 0 5px 0;
					border-bottom: 1px solid @lightBlue;
					@media @large {
						padding: 0 0 15px 0;
					}
				}
				h2 {
					margin: 0;
					bottom: auto;
					top: 30px;
					@media @large {
						top: 45px;
					}
				}
			}
		}

		&.mod-align-center {
			text-align: center;
		}
		
		&:hover,
		&:focus {
			&[class*="media-"]:after { opacity: .90; }
		}
		

		&[class*="media-"]:after {
			color: #fff;
			background: rgba(255,255,255,.6);
			opacity: .6;
			border-radius: 100%;
			display: block;
			text-align: center;
			content: '';
			position: absolute;
			z-index: 12;
			height: 80px;
			width: 80px;
			line-height: 80px;
			top: 50%;
			left: 50%;
			margin-top: -40px;
			margin-left: -40px;
		}
		&.media-video:after { content: 'video'; } // ToDo - doplnit ikonu 
		&.media-photo:after { content: 'photo'; } // ToDo - doplnit ikonu


		// Match tile
		&.mod-match {
			.mod-white-background;
			.mod-align-center;

			.c-tile-match-contest {
				.f-condensed-bold;
				.f-14;
				.t-uppercase;
				line-height: 1.1;
				margin: 4px 0 5px 0;
				color: @text;
				height: 28px;
				overflow: hidden;
				text-overflow: ellipsis;
			}


			&.mod-without-contest {
				.c-tile-match-contest {
					display: none;
				}
				.c-tile-match-opponents-score {
					margin: 5px 0 0 0;
				}
				.c-tile-match-opponents-names {
					display: block;
				}
			}

			.c-tile-match-opponents-score {
				.f-condensed-bold;
				.f-16;
				height: 24px;
				line-height: 24px;
				color: @partner-color-section;
				margin: 0 0 5px 0;
				.icon-flag {
					vertical-align: top;
					margin: 0 5px;
				}
			}
			&.mod-match-started {
				.c-tile-match-opponents-score {
					.f-20;
					color: @anchorColor;
				}
			}

			.c-tile-match-opponents-names {
				.f-condensed-bold;
