/**
Sociální dlaždice

Markup:
<div class="c-tile-row">
	<div class="c-tile-group mod-big">
		<div class="c-tile-item mod-width-1"><a href="" class="c-tile-item-link mod-facebook"><i class="icon-facebook"></i></a></div>
		<div class="c-tile-item mod-width-1"><a href="" class="c-tile-item-link mod-twitter"><i class="icon-twitter"></i></a></div>
		<div class="c-tile-item mod-width-1"><a href="" class="c-tile-item-link mod-youtube"><i class="icon-youtube"></i></a></div>
		<div class="c-tile-item mod-width-1"><a href="" class="c-tile-item-link mod-instagram"><i class="icon-instagram"></i></a></div>	
	</div>
</div>

Styleguide 2.4.
*/

		&.mod-facebook,
		&.mod-twitter,
		&.mod-youtube,
		&.mod-instagram { 
			color: #FFF;
			
			i {
				font-size: 8vw;
				position: absolute;
				display: block;
				margin: 0 auto;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;

				&:before {
					line-height: 100%;
					height: 8vw;
					display: block;
					background-color: transparent;
					margin: -4vw 0 0 -4vw;
					top: 50%;
					left: 50%;
					position: absolute;
					width: 8vw;
					text-align: center;
				}

				@media @small {
					font-size: 4vw; 
					&:before {
						height: 4vw;
						width: 4vw;
						margin: -2vw 0 0 -2vw;
					}
				}
			}

			transition: background-color 300ms linear;

		}

		&.mod-facebook { 
			background-color: @social-facebook; 
			&:hover,
			&:focus { background-color: darken(@social-facebook, 10%); }
		}
		&.mod-twitter { 
			background-color: @social-twitter; 
			&:hover,
			&:focus { background-color: darken(@social-twitter, 10%); }
		}
		&.mod-youtube { 
			background-color: @social-youtube; 
			&:hover,
			&:focus { background-color: darken(@social-youtube, 10%); }
		}
		&.mod-instagram { 
			background-color: @social-instagram; 
			&:hover,
			&:focus { background-color: darken(@social-instagram, 10%); }
		}
	}

	.c-tile-match-link {
		.t-uppercase;
		.f-condensed-bold;
		display: block;
		margin: 0;
		padding: 10px 0;
		background: @tile-link-background;
		border-top: 1px solid @lightBlue;
		position: absolute;
		z-index: 15;
		text-align: center;
		text-decoration: none;

		bottom: 5px;
		left: 5px;
		right: 5px;
		@media @small {
			bottom: 10px;
			left: 10px;
			right: 10px;
		}
	}

	.c-tile-content {
		position: absolute;
		z-index: 13;
		top: 10px;
		left: 10px;
		right: 10px;
		bottom: 10px;
		@media @large {
			top: 15px;
			left: 15px;
			right: 15px;
			bottom: 15px;
		}
		transition: all 160ms linear;
	}

	.c-tile-match-content {
		transition: all 160ms linear;
		position: absolute;
		z-index: 13;
		top: 5px;
		left: 5px;
		right: 5px;
		bottom: 5px;
		@media @large {
			top: 15px;
			left: 15px;
			right: 15px;
			bottom: 15px;
		}
	}

	.c-tile-content {
		h2,
		p { 
			color: #fff; 
			position: absolute;
			bottom: 0;
			text-shadow: 1px 1px 1px rgba(0,0,0,.8);
		}

		h2 { 
			.f-20; 
			.f-condensed-bold;
			.m-t(0);
			.m-b(20px);
		}

		p {
			margin-bottom: 0;
			.f-14;
			.f-condensed; 
		}
	}

	&.mod-width-4 {
		.c-tile-content h2 { 
			.f-36; 
		}
	}

	.tags { opacity: .9; }
}


/**
Text uprostřed dlaždice

Markup:
<div class="c-tile-row">
	<div class="c-tile-group mod-big">
		<div class="c-tile-item mod-width-2">
			<a class="c-tile-item-link mod-white-background" href="">
				<p class="c-tile-text">TEXT<br>UPROSTŘED<br>DLAŽDICE</p>
			</a>
		</div>
	</div>
</div>

Styleguide 2.5.
*/
.c-tile-text {
	text-align: center;
	color: @text;
	position: absolute;
	margin: 0 auto;
  	top: 50%;
  	left: 0;
  	right: 0;
  	transform: translateY(-50%);
}

.container-content .c-tile-group {
	&.mod-big {
		width: 100%; 
		.c-tile-item {
			&.mod-width-1 { width: 25%; }
			&.mod-width-2 { width: 50%; }
			&.mod-width-4 { width: 100%; }
		}
		@media @mini { 
			width: 66.666666%; 
			.c-tile-item {
				&.mod-width-1 { width: 25%; }
				&.mod-width-2 { width: 50%; }
				&.mod-width-4 { width: 100%; }
			}
		}
		@media @medium { 
			width: 75%; 
			.c-tile-item {
				&.mod-width-1 { width: 16.666666%; }
				&.mod-width-2 { width: 33.333333%; }
				&.mod-width-4 { width: 66.666666%; }
			}
		}
	}

	&.mod-small {
		width: 100%; 
		.c-tile-item {
			&.mod-width-1 { width: 25%; }
			&.mod-width-2 { width: 50%; }
			&.mod-width-4 { width: 100%; }
		}
		@media @mini {
			width: 33.333333%; 
			.c-tile-item {
				&.mod-width-1 { width: 50%; }
				&.mod-width-2 { width: 100%; }
				&.mod-width-4 { width: 100%; }
			}
		}
		@media @medium { 
			width: 25%; 
			.c-tile-item {
				&.mod-width-1 { width: 25%; }
				&.mod-width-2 { width: 50%; }
				&.mod-width-4 { width: 100%; }
			}
		}
	}

	&.mod-full {
		width: 100%;
		.c-tile-item {
			&.mod-width-1 { width: 25%; }
			&.mod-width-2 { width: 50%; }
			&.mod-width-4 { width: 100%; }
		}
		@media @medium { 
			.c-tile-item {
				&.mod-width-1 { width: 16.666666%; }
				&.mod-width-2 { width: 33.333333%; }
				&.mod-width-4 { width: 66.666666%; }
			}
		}
	}
}

