/**
Normální tabulka

Markup:
<table>
	<thead>
		<tr>
			<th># ID</th>
			<th>Jméno</th>
			<th>Telefon</th>
			<th><abbr title="Bez české předvolby">Tel.</abbr></th>
		</tr>
	</thead>
	<tbody>
		<tr>
			<td>1.</td>
			<td>Petr Kysela</td>
			<td><a href="#">petr@kysela.biz</a></td>
			<td>739 *** ***</td>
		</tr>
			<td>2.</td>
			<td>Michal Kobelka</td>
			<td><a href="#">michalkobelka@gmail.com</a></td>
			<td>739 *** ***</td>
		</tr>
	</tbody>	
</table>

Styleguide 10.1.
*/

table {
	width: 100%;
	margin: 0 0 @gutter 0;
	border: 1px solid @tbl-border;
	background: #fff;
	td, th {
		text-align: left;
		border: 1px solid @tbl-border;
		padding: 10px 14px;
	}

	thead {
		td, th {
			.t-uppercase;
			background: @tbl-background;
			.f-14;
			.f-condensed-bold;
		}
	}

	tbody {
		tr {
			&:nth-child( even ) {
				td, th {
					background: @tbl-background;
				}
			}

			&:hover {
				td,
				th { background: lighten(@lightBlue, 5%)!important; }
			}
		}
	}

	td {
		a {
			.t-bold;
		}
	}
}

/**
Modifikátory tabulky

.mod-nostripped 	- tabulka bez zvýrazňovaných řádků
.mod-bottomborder 	- tabulka s ohraničením pouze na spodní straně řádků
.mod-lighthead 		- tabulka se světlým záhlavím
.mod-noborder 		- tabulka bez ohraničení
.mod-nohover 		- tabulka bez zvýraznění řádku při hover

Markup:
<table class="{{modifier_class}} {$modifier_class}">
	<thead>
		<tr>
			<th># ID</th>
			<th>Jméno</th>
			<th>Telefon</th>
			<th><abbr title="Bez české předvolby">Tel.</abbr></th>
		</tr>
	</thead>
	<tbody>
		<tr>
			<td>1.</td>
			<td>Petr Kysela</td>
			<td><a href="#">petr@kysela.biz</a></td>
			<td>739 *** ***</td>
		</tr>
			<td>2.</td>
			<td>Michal Kobelka</td>
			<td><a href="#">michalkobelka@gmail.com</a></td>
			<td>739 *** ***</td>
		</tr>
	</tbody>	
</table>

Styleguide 10.2.
*/

table {

	&.mod-nostripped {
		tbody tr:nth-child( even ) {
			td,
			th { background: transparent; }
		}
	}

	&.mod-bottomborder {
		border: 0 none;
		border-bottom: 1px solid @tbl-border;
		td,
		th {
			border: 0 none; 
			border-top: 1px solid @tbl-border;
		}
	}

	&.mod-lighthead {
		thead {
			td,
			th { 
				color: @grey; 
				background: transparent;
			}
		}
	}

	&.mod-noborder {
		border: 0 none;

		th,
		td {
			border: 0 none;
		}
	}

	&.mod-nohover {
		tbody tr:hover {
			td,
			th { background: transparent!important; }
		}
	}


	/**
		PŘEDDEFINOVANÉ TABULKY
		- musí být v bloku s modifikátory
	*/

	&.table-listplayer {
		.mod-nostripped;
		.mod-bottomborder;
		.mod-lighthead;

		td,
		th {
			&:nth-child(5),
			&:nth-child(6) { text-align: center; }
		}
	}
}


