.icon-flag { 
	background-image: url('/images/icon-flags.png'); 
	width: 24px;
	height: 24px;
	display: inline-block;
	opacity: 0; // hack kvůli pozici 0 0 při nezadaném kódu ({{modifier_class}})

	&:before {
		display: none;
		content: '';
	}
}

[class*="icon-flag-"] {
	opacity: 1;
}

@media (-webkit-min-device-pixel-ratio: 2),
       (min-resolution: 192dpi) {
	.icon-flag { background-image: url('/images/icon-flags_@2X.png'); }
}


