/**
Layout

Styl pro rozložení bloků dokumentu
- package: Fotbal.cz
- author: eSports.cz, s.r.o. - Michal Kobelka <michalkobelka@gmail.com>
- author: eSports.cz, s.r.o. - Petr Kysela <petr@kysela.biz>

Styleguide 13.
