.line-main-nav {

	.logo {
	 	position: absolute;
	 	top: 16px;
	 	.mod-hp & {
	 		display: none;
	 	}
	}
	.mod-without-logo & {
		.logo {
			left: 0;
		}
	}
	.mod-with-logo & {
		.logo {
			left: 0;
			@media @large {
				left: 143px;
			}
		}
	}
}

.navigation {

	ul {
		.m-clearfix;
		.m-reset-list;
		height: 52px;
		overflow: hidden;
		.t-uppercase;
		.t-bold;
		font-size: 1rem;
		position: relative;
		top: -1px;
	}

	// Space for logo on large viewport
	.mod-with-logo & {
		@media @small {
			margin-left: 97px;
		}
		@media @large {
			margin-left: 246px;
		}
	}

	.mod-without-logo & {
		@media @small {
			margin-left: 100px;
		}
		@media @large {
			margin-left: 105px;
		}
	}

	.mod-hp & {
		margin-left: 0;
		@media @large {
			margin-left: 110px;
		}
	}

	.menu-item {
		float: left;
		&.is-active, &:hover {
			border-top: 4px solid;
			a {
				padding-top: 0;
				background-color: @midBlue;
				&:after {
					.m-size( 100%, 2px );
					content: " ";
					position: absolute;
					left: 0;
					bottom: -2px;
					background: @midBlue;
				}
			}
		}
	}

	.menu-item-link {
		.f-condensed-bold;
		.t-uppercase;
		.m-block;
		z-index: 100;
		color: #fff;
		position: relative;
		text-decoration: none;
		height: 47px;
		line-height: 47px;
		border-right: 1px solid @border-main-nav;
		&:hover {
			background-color: @midBlue;
		}

		padding: 4px 12px 0;
		@media @large {
			padding: 4px 20px 0;
		}
	}

	.menu-item {
		&-1 {
			display: none;
		}
		&-2 { .menu-item( @sec-repre ); }
		&-3 { .menu-item( @sec-facr ); }
		&-4 { .menu-item( @sec-competitions ); }
		&-5 { .menu-item( @sec-myfootball ); }
		&-6 { .menu-item( @sec-training ); }
		&-7 { .menu-item( @sec-referees ); }
		&-8 { .menu-item( @sec-women ); }
		&-9 { .menu-item( @sec-futsal ); }
		&-10 { .menu-item( @sec-tv ); }
		&-11 { .menu-item( @sec-fanzone ); }
	}
}

.menu-item( @color ) {
	a {
		color: @color;
	}
	&.is-active, &:hover {
		border-top-color: @color;
	}
}
.menu-item-mobile( @color ) {
	a {
		color: @color;
	}
}

.navigation-mobile {
	.lh-translate3d( 100%, 0, 0 );
	position: fixed;
	right: 0;
	width: 90%;
	top: 0;
	bottom: 0;
	background-color: @blue;
	z-index: 999;
	transition: all 300ms ease-in-out;

	@media @small {
		display: none;
	}

	.mod-mobile-nav-open & {
		.lh-translate3d( 0, 0, 0 );		
	}

	.logo {
		display: none;
	}

	> ul {
		.m-reset-list;
		.m-clearfix;
		margin-top: 50px;
	}

	.menu-item-link {
		.t-bold;
		.t-uppercase;
		float: left;
		width: 50%;
		box-sizing: border-box;
		display: block;
		padding: 15px;
		text-decoration: none;
		box-sizing: border-box;
		border-bottom: 1px solid @menu-mobile-border;
		border-right: 1px solid @menu-mobile-border;
	}

	.menu-item {
		&-1 { .menu-item-mobile( #fff ); }
		&-2 { .menu-item-mobile( @sec-repre ); }
		&-3 { .menu-item-mobile( @sec-facr ); }
		&-4 { .menu-item-mobile( @sec-competitions ); }
		&-5 { .menu-item-mobile( @sec-myfootball ); }
		&-6 { .menu-item-mobile( @sec-training ); }
		&-7 { .menu-item-mobile( @sec-referees ); }
		&-8 { .menu-item-mobile( @sec-women ); }
		&-9 { .menu-item-mobile( @sec-futsal ); }
		&-10 { .menu-item-mobile( @sec-tv ); }
		&-11 { .menu-item-mobile( @sec-fanzone ); }
	}

}