.site-logo {

	&.mod-logo-repre {
		display: none;
		@media @large {
			.m-size( 71px, 95px );
			display: block;
			position: absolute;
			top: 15px;
			left: 31px;
			z-index: 100;
			span {
				.m-size( 71px, 95px );
				display: block;
				background: url( '../../img/logo.png' ) 0 0 no-repeat;
			}
		}
	}

	&.mod-logo-facr {
		display: none;
		@media @large {
			.m-size( 86px, 120px );
			display: block;
			position: absolute;
			z-index: 100;
			span {
				.m-size( 86px, 120px );
				display: block;
				background: url( '../../img/logo-facr.png' ) 0 0 no-repeat;
			}

			.mod-hp & {
				top: -60px;
				left: 10px;
			}
			.mod-section & {
				top: 15px;
				left: 31px;
			}
		}
	}

	.mod-without-logo & {
		display: none;
	}
}