.form {
	width: 100%;
}

.form-pair { 
	.m-clearfix(); 
	.m-b(15px); 

	&:last-child { .m-b(0); }

	&.mod-foot {
		background-color: @tbl-background;
		border-top: 1px solid @tbl-border;
		.p-t(10px);
	}
}

.form-label,
.form-input {
	label {
		.f-base-bold;
		line-height: 20px;
		color: @midBlue;
	}
}

.form-label label {
	.f-base-bold;
	&:first-letter { .t-uppercase; }
}

.form-input label {
	line-height: 20px;
	display: block;
	.m-b(5px);
}

@media @small {
	.form-label {
		float: left;
		width: 100%;
		max-width: 180px;
		min-height: 1px;
		text-align: right;

		label { line-height: 42px; }
	}
	.form-input {
		  width: 100%;
		  max-width: 750px;
		  min-height: 1px;
		  .p-l(200px);
		  box-sizing: border-box;

		  label:first-child { .p-t(10px); }
	}
	.form-pair.mod-small {
		max-width: 360px;
		.m-l(auto);
		.m-r(0px);

		.form-label { 
		  	box-sizing: border-box;
			max-width: 40%; 
			.p-r(20px);
		}
		.form-input { 
			.p-l(40%);
			max-width: 100%; 
		}
	}
}
