/**
Nadpisy

Markup:
<h1 class="h1">Vstupenky na derby Slovensko - Česko na pobočkách ČEDOKU</h1>
<h2 class="h2">Vstupenky na derby Slovensko - Česko na pobočkách ČEDOKU</h2>
<h3 class="h3">Vstupenky na derby Slovensko - Česko na pobočkách ČEDOKU</h3>
<h4 class="h4">Vstupenky na derby Slovensko - Česko na pobočkách ČEDOKU</h4>
<h5 class="h5">Vstupenky na derby Slovensko - Česko na pobočkách ČEDOKU</h5>
<h6 class="h6">Vstupenky na derby Slovensko - Česko na pobočkách ČEDOKU</h6>

Styleguide 6.3.
*/

.h1 {
	.f-28;
	.f-condensed;
	.p(0 0 0 10px);
	.m(0 0 15px 0);

	border-left: solid @sec-repre;
	color: @blue;
	
	border-width: 2px;
	@media @small {
		.f-36;
		.m(0 0 30px 0);
		.p(2px 0 2px 10px);
		padding-left: 20px;
		border-width: 4px;
	}
	@media @large {
		.f-40;
		padding-left: 30px;
		border-width: 6px;
	}
}

.h2 {
	.f-20;
	.f-condensed-bold;
	color: @blue;
}

/**
Zvýraznění textu

.t-uppercase	- text kapitálkami
.t-bold			- tučný text
.t-italic		- text kurzívou <strong style="color:red">@deprecated</strong>
.t-underline	- podtržený text

Markup:
<p>
	Šlo vy úzce hnutí a 2800 i <span class="{{modifier_class}} {$modifier_class}">vybavení ukázky</span> pyšně chráněna učí.
</p>

Styleguide 6.4.
*/

.t-uppercase { text-transform: uppercase; }

.t-bold { font-weight: 700; }

.t-italic { font-style: italic; }

.t-underline { text-decoration: underline; }


/**
Definiční list <dl>

Markup:
<dl>
	<dt>Bernát Luděk</dt>
	<dd>člen výpravy / member of delegation</dd>
	<dt>Bruückner Karel</dt>
	<dd>člen výpravy / member of delegation</dd>
</dl>

Styleguide 6.5.
*/

dl {
	.m(20px 0 20px 0);
	dt { 
		.f-base-bold;
		.f-20;
		.m-b(5);
	}
	dd {
		.m(0 0 15px 0);
	}
}
