.list {
	.p(0);
	.m(0);
	display: block;
	width: 100%;

	.list-item {
		.m-clearfix();
		display: block;
		box-sizing: border-box;
		background: #fff;
		border: 1px solid @list-border;
		border-bottom: 0 none;
		.p(0);

		&:hover {
			background: lighten(@lightBlue, 5%);
		}
	}

	.list-link {
		.p(15px 10px);
		box-sizing: border-box;
		display: table;
		width: 100%;
		text-decoration: none;
		color: @text;
	}

	.list-date,
	.list-image,
	.list-text,
	.list-name,
	.list-datagrid,
	.list-btn-container {
		display: table-cell;
		vertical-align: middle;
		.p-r(30px);

		line-height: 24px;
		min-height: 24px;

		&:first-child { .p-l(25px); }
		&:last-child { .p-r(25px); }
	}

	.list-date {
		width: 13%;
		color: @grey;
		.f-condensed;
		.p-t(8px);
		.p-b(8px);
	}

	.list-image { 
		width: 40px; 
		.p-r(20px);
		.m-clearfix();
		img { 
			max-width: 100%; 
			float: left;
			display: block;
		}
	}

	.list-text {
		.f-condensed;
		.p-t(8px);
		.p-b(8px);
	}


}

