.list {

	.list-name {
		.f-condensed-bold;
		.f-20;
		
		&.mod-big { width: 22%; }
		&.mod-small { width: 15%; }
	}

	.list-btn-container,
	.list-datagrid  {
		display: table-cell;
		vertical-align: middle;
	}

	.list-datagrid { 
		width: 50%; 
		.p-r(0);
	}
	.list-btn-container { width: 70px; }

	.list-event {
		display: table;
		width: 100%;
		.m-clearfix();
	}
	.list-event_cell {
		display: table-cell;
		vertical-align: middle;
		
		.p-r(30px);

		span { vertical-align: middle; }

		&.mod-w45 { width: 45%; }
		&.mod-w40 { width: 40%; }
		&.mod-w30 { width: 30%; }
		&.mod-w15 { width: 15%; }
	}

	.list-event_datetime,
	.list-event_score {
		display: block;
	}
	.list-event_datetime {
		.f-condensed-bold;
		color: @grey;
		line-height: 18px;

		&.mod-big {
			.f-condensed-bold;
		}
	}
	.list-event_country {
		.f-condensed-bold;
		color: @darkBlue;
		line-height: 26px;
		display: inline-block;
		.m-r(10%);
		&:last-child { .m-r(0); }

		.icon-flag { 
			float: left; 
			.m-r(8px);
		}
	}
	.list-event_score {
		.f-condensed-bold;
		text-align: center;
		color: @list-score-default;
		text-decoration: underline;

		&.mod-blue { color: @list-score-blue; }
		&.mod-green { color: @list-score-green; }
		&.mod-red { color: @list-score-red; }
		&.mod-big {
			.f-base-bold; 
			.f-20;
		}
	}
}


