.box {
	.m-clearfix;
	box-sizing: border-box;
	border: 1px solid @lightBlue;
	margin-bottom: @gutter;
	&:last-child {
		margin-bottom: 0;
	}

	a {
		color: @red;
	}

	&.box-document {
		.mod-small;
		.mod-white;

		.box-document-body {
			padding: 0 5px;
		}
		.box-document-date {
			.f-condensed;
			.f-14;
			margin: 0;
			color: @grey;
			line-height: 1;
			.t-bold;
		}
		.box-document-title {
			.f-condensed-bold;
			.f-20;
			margin: 0;
		}
		.box-document-link {
			.f-condensed;
			.f-16;
			margin: 30px 0 0;
			a {
				margin: 0 0 0 4px;
			}
		}
	}

	&.box-full {
		.mod-full;
		.mod-grey;
		clear: both;

		.box-title {
			.f-16;
			line-height: 1;
			margin: 0 0 3px;
			color: @grey;
			.f-condensed-bold;
			.t-uppercase;
		}

		.box-full-header {
			margin: 0 0 @gutter;
			@media @medium {
				margin: 0;
				float: left;
				width: 195px;
			}
			@media @large {
				width: 207px;
			}
		}

		.box-full-body {

			@media @medium {
				float: right;
				width: 405px;
			}
			@media @large {
				width: 420px;
			}
		}

		.box-full-title {
			.f-condensed-bold;
			.f-20;
			line-height: 1.3;
			margin: 0 0 4px;
		}

		.box-full-date {
			.f-condensed;
			.f-14;
			color: @grey;
		}

		.box-full-link {
			.f-condensed;
			margin: 0;
			line-height: 1;
		}

	}

	.box-gallery {
		.m-reset-list;
		.m-clearfix;

		.box-gallery-item {
			.m-size( 90px );
			float: left;
			margin: 0 10px 10px 0;
			@media @small {
				margin-bottom: 0;
			}
			@media @medium {
				margin: 0 15px 0 0;
			}
			@media @large {
				margin: 0 @gutter 0 0;
			}
			&:last-child {
				margin: 0;
			}
		}
		.box-gallery-link {
			.m-size( 90px );
			display: block;
		}
		.box-gallery-img {
			.m-size( 90px );
			display: block;
			background: #eee;
			background-position: 50% 50%;
			background-size: cover;
			background-repeat: no-repeat;
		}
	}

	.box-documents {
		.m-reset-list;
		.box-document-item {
			margin: 0 0 5px 0;
			&:last-child {
				margin-bottom: 0;
			}
		}
		.box-document-link {
			.f-condensed;
			span[class^=icon] {
				margin-right: 10px;
			}
		}
	}

	&.mod-bottom-shadow {
		box-shadow: 0 3px 3px -3px rgba(0,0,0,.2);
	}

	&.mod-padding {
		.p(5px 36px 10px 36px);
	}

	&.mod-grey {
		background: @tbl-background;
	}

	&.mod-white {
		background: #fff;
		.box-title {
			.f-16;
			line-height: 1;
			margin: 0;
			color: @grey;
			.t-bold;
			.t-uppercase;
			border-bottom: 1px solid @lightBlue;
			padding: 0 0 15px 0;
			margin: 0 0 15px 0;
			text-indent: 5px;
		}
	}

	&.mod-full {
		width: 100%;
		float: none;

		padding: 10px;
		@media @medium {
			padding: 15px;	
		}
		@media @large {
			padding: 30px;	
		}
	}

	&.mod-small {
		padding: 20px;
		@media @small {
			width: 250px;
			float: left;
			margin-right: @gutter;
		}
	}

}