.row-section-header {
	.col-60 {
		float: none;
		width: auto;
		@media @small {
			width: 60%;
			float: left;
		}
	}
	.col-40 {
		display: none;
		@media @small {
			display: block;
		}
	}
}

.section-logo {
	display: block;
	position: relative;
	right: 0;
	text-decoration: none;

	span {
		.f-base-bold;
		.t-uppercase;
		color: @partner-color-section;
		margin: 17px 0 0 20px;
		float: left;
		@media @small {
			float: right;
			margin: 8px 20px 0 0;
			width: 100px;
		}
	}
	img {
		float: left;
		@media @small {
			float: right;
		}
	}
}