@font-face {
  font-family: 'icons';
  src: url('../../font/icons.eot?93764254');
  src: url('../../font/icons.eot?93764254#iefix') format('embedded-opentype'),
       url('../../font/icons.woff?93764254') format('woff'),
       url('../../font/icons.ttf?93764254') format('truetype'),
       url('../../font/icons.svg?93764254#icons') format('svg');
  font-weight: normal;
  font-style: normal;
}

 
[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "icons";
  font-style: normal;
  font-weight: normal;
  speak: none;
 
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
 
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
     
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
 
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
 
}
 