.line-site-heading {
	z-index: 1000;
}

.site-heading {
	.f-condensed-bold;
	.f-20;
	margin: 0;
	color: #fff;
	position: relative;
	height: 50px;
	line-height: 50px;
	padding-left: 15px;
	float: left;
	cursor: pointer;

	&:after {
		position: absolute;
		content: " ";
		border-width: 7px;
		border-style: solid;
		border-color: #fff transparent transparent transparent;
		right: -20px;
		top: 45%;
		margin-top: -1px;
	}

	@media @small {
		.t-uppercase;
		.f-28;
		cursor: auto;
		float: none;
		height: 80px;
		line-height: 80px;
		padding-left: 35px;

		&:before {
			.m-size( 3px, 40px );
			content: " ";
			position: absolute;
			top: 18px;
			left: 0;
			background: @sec-repre;
		}

		&:after {
			display: none;
		}
	}

	.mod-with-logo & {
		@media @large {
			margin-left: 143px;
		}		
	}

	.mod-without-logo & {
		margin-left: 0;
	}

}

.nav-opener {
	.f-condensed-bold;
	.f-20;
	color: #fff;
	float: right;
	margin: 0 15px 0 0;
	height: 50px;
	line-height: 50px;
	position: relative;
	cursor: pointer;

	&:after {
		position: absolute;
		content: " ";
		border-width: 7px;
		border-style: solid;
		border-color: #fff transparent transparent transparent;
		left: -20px;
		top: 45%;
		margin-top: -1px;
	}

	@media @small {
		display: none;
	}
}

