.column-left {
	top: 0;
	bottom: 0;
	z-index: 50;
	position: fixed;
	background: @blue;
	.lh-translate3d( -100%, 0, 0 );
	transition: all 300ms ease-in-out;

	@media @small {
		.lh-translate3d( 0, 0, 0 ) !important;
		z-index: 50 !important;
		transition: none !important;
	}

	// Mobile menu opened
	.mod-mobile-menu-open & {
		.lh-translate3d( 0, 0, 0 );
		z-index: 500;
	}
}

.menu {
	position: absolute;
	top: 252px;
	bottom: 0;
	width: 100%;
	overflow-y: auto;
	.menu-top-level, .menu-submenu {
		.m-reset-list;
	}

	.menu-item {

		&.mod-with-submenu {
			> .menu-item-link {
				&:after {
					.m-size( 10px, 25px );
					.f-20;
					// background: url('../../img/menu-arrow.png') 50% 50% no-repeat;
					position: absolute;
					right: 20px;
					top: 50%;
					margin-top: -11px;
					transition: transform 250ms ease-in-out, margin 250ms ease-in-out;
					font-family: 'icons';
					content: '\e809';
					background: transparent;
					font-weight: normal;
				}
			}
		}


		> .menu-submenu {
			.menu-item-link { .f-condensed; }
			background: @menu-submenu;
		}

		&.mod-is-opened {
			> .menu-submenu {
				max-height: 1000px;
			}
			> .menu-item-link {
				&:after {
					transform: rotate( 90deg );
					margin-top: -13px;
				}
			}
		}

	}

	.menu-item-link {
		.f-condensed-bold;
		.f-16;
		color: @lightBlue;
		display: block;
		text-decoration: none;
		border-bottom: 1px solid @menu-dark-border;
		border-top: 1px solid @menu-light-border;
		position: relative;
		padding: 10px 15px;
		@media @medium {
			padding: 10px 30px;	
		}
		&:hover {
			color: @menu-color-hover;
		}
	}
	.menu-item-heading {
		.f-20;
		.f-condensed-bold;
		.t-uppercase;
		line-height: 1;
		color: @menu-heading;
		margin: 0;
		border-top: 1px solid @menu-light-border;
		border-bottom: 1px solid @menu-dark-border;
		padding: 15px;
		@media @medium {
			padding: 20px 30px;	
		}
	}

	.menu-submenu {
		height: auto;
		max-height: 0;
		overflow: hidden;
		transition: max-height 300ms ease-in-out;
		> .menu-item {
			> .menu-item-link {
				border-bottom: 1px solid @menu-submenu-dark-border;
				border-top: 1px solid @menu-submenu-light-border;
				padding-left: 15px;
				@media @medium {
					padding-left: 30px;
				}
				@media @large {
					padding-left: 40px;
				}
			}
		}
	}
}

.menu-curtain {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: #000;
	z-index: 200;
	visibility: hidden;
	opacity: 0;
	transition: opacity 300ms ease-in-out;
	cursor: pointer;
	@media @small {
		display: none;
	}

	.mod-mobile-menu-open &, .mod-mobile-nav-open & {
		visibility: visible;
		opacity: 0.4;
	}
}

body {
	&.mod-mobile-menu-open, &.mod-mobile-nav-open {
		overflow: hidden;
	}
}