 // Fonts
body {
 	.f-base;
}
 
html {
	font-size: 14px;
	@media @small {
		font-size: 16px;
	}
}

a {
	color: @anchorColor;

	&:hover,
	&:focus { color: darken(@anchorColor, 10%); }
}

/**
Velikosti

Velikost fontů na webu (px): 
12, 14, <b>16 (základní velikost html{font-size})</b>, 20, 24, 28, 36, 40, 50 

.f-12	- velikost 12px
.f-14	- velikost 14px
.f-16	- velikost 16px
.f-20	- velikost 20px
.f-24	- velikost 24px
.f-28	- velikost 28px
.f-36	- velikost 36px
.f-40	- velikost 40px
.f-50	- velikost 50px

Markup:
<p class="{{modifier_class}} {$modifier_class}">
	Šlo vy úzce hnutí a 2800 i vybavení ukázky.
</p>

Styleguide 6.2.
*/

// Font sizes
// 1rem = 16px;
// .f-14 = 14px = 0.875rem
// .f-16 = 16px = 1rem
// .f-18 = 18px = 1.125rem
// Available font classes: from .f-10 to .f-40
//
// .font-size (@size) when (@size > 9) {
//   	.font-size((@size - 1));    // next iteration
//   	.f-@{size} {
// 		font-size: @size/16rem;
//   	}
// }
//
// .font-size( 40 );

.f-12 { font-size: 12/16rem; }
.f-14 { font-size: 14/16rem; }
.f-16 { font-size: 16/16rem; }
.f-20 { font-size: 20/16rem; }
.f-24 { font-size: 24/16rem; }
.f-28 { font-size: 28/16rem; }
.f-36 { font-size: 36/16rem; }
.f-40 { font-size: 40/16rem; }
.f-50 { font-size: 50/16rem; }


// Basic typography
.formatted-text {

	p {
		margin: 0 0 @gutter 0;
	}

	ul {
		.m-reset-list;
		margin: 0 0 @gutter 0;
		list-style: none;
		li {
			padding: 0 0 0 38px;
			position: relative;
			&:before {
				display: block;
				.m-size( 4px );
				content: " ";
				position: absolute;
				left: 7px;
				top: 8px;
				border-radius: 50%;
				border: 2px solid @red;
			}
		}
	}

}