/**
Article

Styl pro stránku článku a jeho komponenty
- package: Fotbal.cz
- author: eSports.cz, s.r.o. - Michal Kobelka <michalkobelka@gmail.com>
- author: eSports.cz, s.r.o. - Petr Kysela <petr@kysela.biz>

Markup:
<div class="article">
	<div class="article-header mod-without-photo">
		<p class="article-date">3. 5. 2015 - Karel Čapek</p>
		<h1 class="article-heading">Otevřte koše tys čem sáčku mezek šedivých!</h1>
	</div>
	<div class="article-header mod-with-photo">
		<div class="article-photo">
			<span class="article-photo-span" style="background-image: url(public/image.png)"></span>
		</div>
		<p class="article-date">3. 5. 2015 - Karel Čapek</p>
		<h1 class="article-heading">Otevřte koše tys čem sáčku mezek šedivých!</h1>
	</div>
	<div class="article-content formatted-text">
		<div class="article-perex">
			<p>Vozů a uf, v vůl od velmi. Otázka štábním ať půlka máte kupodivu tvrdě, tmy hry z za vylezl či břehů z životem odpouštěl jé sklepnice. Holčička sami prvý uznaných tě přítel otisky.</p>
		</div>
		<p>411 nu trní bok podívala slíbil růže uvalit. Pana v&nbsp;chci jedl ji husa zbavili. Sáčku ruku tvé co cit zachoval dnů hloub. Až ve ber pohnána plic skvělý prostřed strašlivého he lujza dán barevná naschvál. </p>
		<p>Jemné on tě mě kravály si krutě? Nemožné o&nbsp;snu určitě mě váš v&nbsp;dorozumíme či 11 do&nbsp;přestávkách pakujte rakvi nadávat vši! Půjčte stopa nejhlubším co toje válce. Ex máknout? Té himl domácí osmý ta bot jasný – mého ke oka nebreč vzácné mi pronikl! </p>
		<ol>
			<li>Jízlivě kladivem považuje o&nbsp;votant, z&nbsp;ně mu.</li>
			<li>Hojila sami oukropeček zavřeme nejistý dá flašinetáře vyběhla.</li>
			<li>Do zavlečenou celá plány lži vytažena.</li>
			<li>Boj hornatý ba mával ministra křečovitými krádeží!</li>
			<li>Stůl tu přímo sice pyžama u&nbsp;opětoval nenechal šestatřicet k&nbsp;starostlivě.</li>
		</ol>
		<ul>
			<li>Větu světů, dá uf frkal plečko, šlo tedy mluvit muk republice.</li>
			<li>Té zlo, ta styděl.</li>
			<li>Milý u&nbsp;zima druhé.</li>
			<li>My válel uvedeného zamčen do&nbsp;boty ó košilí a&nbsp;ví štábu váš svým temně v&nbsp;šly punčocha.</li>
			<li>Nešikovná šaltr baum v&nbsp;provaz.</li>
		</ul>
		<p>V sna au dává řku malinký stál psa přímo živá výslech. A 19 korespondent mnul zapře o doporučuje nanejvýš. Kde kozlík drclo leda deky jiném ukáže nemohou o mat válce. Ti dveří patnáct sad ze civilisty síni?</p>
	</div>
	<div class="article-share">
		Sdílecí tlačítka
	</div>
</div>

Styleguide 1
*/

.article {
	.m-clearfix;
	background: #fff;
	margin: 0;
	color: @text;

	.article-header {
		.m-clearfix;
		padding: 20px;
		border-bottom: 1px solid @lightBlue;
		margin: 0 0 20px 0;

		@media @large {
			margin: 0 0 40px 0;
			padding: 40px;
		}

		.article-heading {
			.f-condensed-bold;
			.f-28;
			line-height: 1.2;
			color: @darkBlue;
			margin: 0;
			@media @small {
				.f-28;
			}
			@media @medium {
				.f-36;
			}
			@media @large {
				.f-40;
			}
		}

		.article-photo {
			display: none;
			@media @small {
				display: block;
				.m-size( 100px );	
			}
			@media @medium {
				.m-size( 180px );
			}
			@media @large {
				.m-size( 220px );
			}
		}

		.article-photo-span {
			display: block;
			@media @small {
				.m-size( 100px );	
			}
			@media @medium {
				.m-size( 180px );
			}
			@media @large {
				.m-size( 220px );
			}
			background-position: 50% 50%;
			background-size: cover;
		}

		.article-date {
			.f-condensed;
			.f-14;
			color: @grey;
		}

		&.mod-with-photo {
			.article-photo {
				float: left;
			}
			.article-heading {
				@media @small {
					float: right;
					width: 365px;
				}
				@media @medium {
					width: 450px;
				}
				@media @large {
					width: 650px;	
				}
			}
			.article-date {
				@media @small {
					margin: 5px 0;
					float: right;
					width: 365px;
				}
				@media @medium {
					margin: 15px 0;
					width: 450px;
				}
				@media @large {
					margin: 25px 0;
					width: 650px;
				}
			}
		}

		&.mod-without-photo {
			.article-date {
				margin: 0 0 15px 0;
			}
		}
	}

	.article-share {
		text-align: center;
		margin: 20px 0;
		clear: left;
		@media @large {
			float: left;
			margin: 0;
			width: 200px;
		}
	}

	.article-content {
		.f-base;
		box-sizing: border-box;
		line-height: 1.5;
		padding: 0 20px;

		@media @large {
			float: right;
			padding: 0 70px 0 0;
			width: 780px;
		}

		.article-perex {
			.f-20;
			margin: 0 0 30px 0;
		}
	}

	.article-tags {
		clear: both;
		border-top: 1px solid @lightBlue;
		padding: 20px;

		@media @large {
			padding: 20px 40px;
		}

	}
}

