.footernav {
	.m-clearfix;
	display: none;
	
	@media @small {
		padding: 20px 0 50px 0;
		display: block;
	}
	
	@media @large {
		padding: 50px 0 90px 0;	
	}

	ul {
		.m-reset-list;
	}
	a {
		text-decoration: none;
	}

	.menu-top-level {
		.m-clearfix;

		> .menu-item {
			box-sizing: border-box;
			display: block;
			padding: 0 20px;
			margin-bottom: 20px;

			@media @medium {
				width: 20%;
				float: left;
				margin-bottom: 0;
			}

			@media @large {
				padding: 0 0 0 40px;
			}

			> a {
				.f-condensed-bold;
				.f-20;
				display: inline-block;

				margin-bottom: 15px;
				@media @medium {
					margin-bottom: 30px;
				}
			}
		}
	}

	.menu-submenu {
		.menu-item {
			@media @small {
				display: inline-block;
				margin: 0 5px 5px 0;
				padding: 0 8px 0 0;
				border-right: 1px solid @blue;
				&:last-child {
					border: 0;
				}
			}
			@media @medium {
				display: block;
				margin: 0 0 10px 0;	
				padding: 0;
				border: 0;
			}	
		}
		.menu-item-link {
			.f-14;
			display: block;
			padding: 10px 5px;
			border-bottom: 1px solid @blue;
			@media @small {
				display: inline-block;
				padding: 0;
				border: 0;
			}
		}
	}

	.menu-item-link {
		color: #fff;
	}
}